<template>
  <div class="Reject-list">
    <div class="" v-if="table_list_show">
      <div class="clearfix Button-tab">
        <p class="left" v-bind:class="{ active: isActive }" @click="Personal_tab">{{$t("common.Personal_rejection")}}</p>
        <p class="left" v-bind:class="{ active: !isActive }" @click="Institutional_tab">{{$t("common.Enterprise_rejection")}}</p>
      </div>
      <div class="Personal" v-if="isActive">
        <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.Personal_number")}}&nbsp</p>
              <Input placeholder="" style="width: 150px"></Input>
            </Col>
            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.Username_top")}}&nbsp</p>
              <Input placeholder="" style="width: 150px"></Input>
            </Col>

            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.ID_number")}}&nbsp</p>
              <Input placeholder="" style="width: 150px;margin-right:10px;"></Input>
              <Button type="primary">{{$t("common.Query")}}</Button>
            </Col>
        </Row>
        <Table highlight-row :columns="Personal" :data="PersonalData" @on-row-click="PersonalTableData" :style="{marginTop:'20px'}"></Table>
        <Row type="flex" justify="center" class="code-row-bg page-data">
          <Col span="24">
            <Page :total="dataCount" :current.sync="current1" :page-size="pageSize" @on-change="changepage"></Page>
          </Col>
        </Row>
      </div>
      <div class="Institutional" v-if="!isActive">
        <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.Company_number")}}&nbsp</p>
              <Input placeholder="" style="width: 150px"></Input>
            </Col>
            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.Company_name")}}&nbsp</p>
              <Input placeholder="" style="width: 150px"></Input>
            </Col>

            <Col :xs="12" :sm="12" :lg="6">
              <p>{{$t("common.type_enterprise")}}&nbsp</p>
              <Select style="width:150px;margin-right:10px;">
                  <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>
              <Button type="primary">{{$t("common.Query")}}</Button>
            </Col>
        </Row>
        <Table highlight-row :columns="Institutional" :data="InstitutionalData" @on-row-click="InstitutionalTableData" :style="{marginTop:'20px'}"></Table>
        <Row type="flex" justify="center" class="code-row-bg page-data">
          <Col span="24">
            <Page :total="dataCount1" :current.sync="current2" :page-size="pageSize" @on-change="changepage_Ins"></Page>
          </Col>
        </Row>
      </div>
    </div>
    <div class="Reject-personal" v-if="Personal_detail">
      <Row type="flex" justify="start" class="code-row-bg personal-detail">
        <Col span="10" class="mgl-20">
          <span class="font-16">个人详细信息</span>
        </Col>
        <Col span="5" class="font-16">
          <div class="success clearfix">
            <Icon type="ios-close-outline" size="30" color="red" class="left"></Icon>
             <b class="left">已驳回</b>
          </div>
        </Col>
        <Col span="8" class="text-right">
          <Button type="warning" @click="Personal_detail = false ; table_list_show = true ; isActive = true">返回</Button>
        </Col>
      </Row>
      <Row type="flex" justify="start" class="code-row-bg personal-name">
        <Col span="6" class="mgl-20">
          <span>姓名：</span>
          <span>李静</span>
        </Col>
        <Col span="6">
          <span>身份证号：</span>
          <span>137123456783216548</span>
        </Col>
      </Row>
      <Row type="flex" justify="space-around" class="code-row-bg personal-certificates">
        <Col span="5">
          <h4 class="font-16">身份证正面</h4>
          <div class="upload">
            <img src="../../assets/p1.jpg" alt="">
          </div>
          <Button type="info" @click="showpic01 = true">预览</Button>
          <Modal
              v-model="showpic01"
                title="请看大图"
              >
              <img src="../../assets/p1.jpg" alt="" width="100%">
          </Modal>
        </Col>
        <Col span="5">
          <h4 class="font-16">身份证反面</h4>
          <div class="upload">
            <img src="../../assets/p2.jpg" alt="">
          </div>
          <Button type="info" @click="showpic02 = true">预览</Button>
          <Modal
              v-model="showpic02"
                title="请看大图"
              >
              <img src="../../assets/p2.jpg" alt="" width="100%">
          </Modal>
        </Col>
        <Col span="5">
          <h4 class="font-16">手持身份证</h4>
          <div class="upload">
            <img src="../../assets/p3.jpg" alt="">
          </div>
          <Button type="info" @click="showpic03 = true">预览</Button>
          <Modal
              v-model="showpic03"
                title="请看大图"
              >
              <img src="../../assets/p3.jpg" alt="" width="100%">
          </Modal>
        </Col>
      </Row>
      <div class="Reject-reason">
        <p>驳回原因</p>
        <div class="reason-box">身份证正面照不清晰</div>
      </div>
    </div>
    <div class="Reject-enterprise" v-if="Institutional_detail">
      <Row type="flex" justify="start" class="code-row-bg personal-detail">
        <Col span="10" class="mgl-20">
          <span class="font-16">企业详细信息</span>
        </Col>
        <Col span="5">
          <div class="success clearfix">
            <Icon type="ios-close-outline" size="30" color="red" class="left"></Icon>
             <b class="left">已驳回</b>
          </div>
        </Col>
      </Row>
      <div class="enterprise-title">
        <b class="font-16">企业基本信息</b>
      </div>
      <table class="basic-info">
        <tr>
          <th>统一社会信用代码</th>
          <td>统一社会信用代码</td>
          <th>组织机构代码</th>
          <td>00000000-9</td>
          <th>税务登记号</th>
          <td>123456784545111</td>
        </tr>
        <tr>
          <th>企业名称</th>
          <td>北京全时有限公司</td>
          <th>单位类型</th>
          <td>企业</td>
          <th>营业执照所在地</th>
          <td>北京市</td>
        </tr>
        <tr>
          <th>注册资金</th>
          <td>1，000，000，000 元</td>
          <th>联系电话</th>
          <td>13112345678</td>
          <th>公司成立日期</th>
          <td>2012-10-20</td>
        </tr>
        <tr>
          <th>实缴资本</th>
          <td>1，000，000，000 元</td>
          <th>注册地址</th>
          <td>北京市海淀区</td>
          <th>营业范围</th>
          <td>金融</td>
        </tr>
        <tr>
          <th>营业期限</th>
          <td>长期</td>
          <th>企业法人</th>
          <td>胡歌</td>
          <th>法人身份证号</th>
          <td>123654789987456321</td>
        </tr>
        <tr>
          <th>传真</th>
          <td>222-2222222</td>
          <th>营业执照登记机关</th>
          <td>北京市</td>
          <th>是否存在境外交易</th>
          <td>否</td>
        </tr>
        <tr>
          <th>经营区域</th>
          <td>全国</td>
        </tr>
      </table>
      <div class="enterprise-title">
        <b class="font-16">企业认证信息</b>
      </div>
      <Row type="flex" justify="start" class="code-row-bg ">
        <Col span="5">
          <h4 >营业执照副本扫描件</h4>
          <div class="upload">
            <img src="../../assets/p4.jpg" alt="">
          </div>
          <Button type="info" @click="showpic04 = true">预览</Button>
          <Modal
              v-model="showpic04"
                title="请看大图"
              >
              <img src="../../assets/p4.jpg" alt="" width="100%">
          </Modal>
        </Col>
        <Col span="10" class="mgl-100">
          <div class="Reject-reason">
            <p>驳回原因</p>
            <div class="reason-box">营业执照副本照片不清晰</div>
          </div>
        </Col>
      </Row>
    </div>
  </div>

</template>

<script>
import {Area_code} from '../../../static/js/Area_code'
export default {
    name:'Reject_list',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            ajaxHistoryData1:[],
              // 初始化信息总条数
            dataCount1:0,
              // 每页显示多少条
            pageSize:10,
            showpic01:false,
            showpic02:false,
            showpic03:false,
            showpic04:false,
            current1:1,
            current2:1,
            PersonalData: [],
            InstitutionalData:[],
            isActive:true,
            cityList: [
              {
                  value: 'All',
                  label: this.$t("common.Central_enterprise")
              },
              {
                  value: 'Yes',
                  label: this.$t("common.State_owned_enterprise")
              },
              {
                  value: 'No',
                  label: this.$t("common.Private_enterprise")
              }
            ],
            Personal: [
                {
                    title:this.$t("common.user_number"),
                    key: 'user_number',
                    align:'center',
                },
                {
                    title: this.$t("common.Username"),
                    key: 'username',
                    align:'center'
                },
                {
                    title: this.$t("common.ID_number"),
                    key: 'ID',
                    align:'center'
                },
                {
                    title: this.$t("common.Bank_card_account"),
                    key: 'bank_ID',
                    align:'center'
                },
                {
                    title: this.$t("common.Bank"),
                    key: 'opening_bank',
                    align:'center'
                },
                {
                    title: this.$t("common.Certification_status"),
                    key: 'state',
                    align:'center'
                }
            ],
            Institutional: [
                {
                    title: this.$t("common.Company_number"),
                    key: 'Inst_number',
                    align:'center',
                },
                {
                    title: this.$t("common.Company_name"),
                    key: 'Instname',
                    align:'center'
                },
                {
                    title: this.$t("common.phonenum"),
                    key: 'phoneNum',
                    align:'center'
                },
                {
                    title: this.$t("common.business_entity_name"),
                    key: 'name',
                    align:'center'
                },
                {
                    title: this.$t("common.Company_address"),
                    key: 'address',
                    align:'center'
                },
                {
                    title: this.$t("common.Certification_status"),
                    key: 'state',
                    align:'center'
                }
            ],
            data1: this.mockTableData1 (),
            data2: this.mockTableData2 (),
            Personal_info:null,
            Institutional_info:null,
            table_list_show:true,
            Personal_detail:false,
            Institutional_detail:false
        }
    },
    methods:{
      Personal_tab (){
        this.isActive = true;
      },
      Institutional_tab (){
        this.isActive = false;
      },
      //数据初始化
      handleListPersonal(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.PersonalData = this.ajaxHistoryData;
          }else{
              this.PersonalData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      handleListInstitutional(){
          // 保存取到的所有数据
          this.ajaxHistoryData1 = this.data2
          this.dataCount1 = this.data2.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data2.length < this.pageSize){
              this.InstitutionalData = this.ajaxHistoryData1;
          }else{
              this.InstitutionalData = this.ajaxHistoryData1.slice(0,this.pageSize);
          }
      },
      //分页
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.PersonalData = this.ajaxHistoryData.slice(_start,_end);
      },
      changepage_Ins(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.InstitutionalData = this.ajaxHistoryData1.slice(_start,_end);
      },
      //模拟数据
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  user_number: 'DG0000'+i,
                  username: parseInt(Math.random()*(3-1)+0)>0?'陈道明':'李连杰',
                  ID: this.getId_no(),
                  bank_ID: '60138264020029515'+parseInt(Math.random()*(10-1)+1),
                  opening_bank:parseInt(Math.random()*(3-1)+0)>0?'北京银行':'浦发银行',
                  state:'已驳回'
              })
          }
          return data;
      },
      mockTableData2 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              data.push({
                  Inst_number: 'DG0000'+i,
                  Instname: parseInt(Math.random()*(3-1)+0)>0?'帝秋股份有限公司':'内蒙古奶牛场',
                  phoneNum: this.getMoble(),
                  name: parseInt(Math.random()*(3-1)+0)>0?'黄宗洛':'杨幂',
                  address:parseInt(Math.random()*(3-1)+0)>0?'北京市朝阳区霄云路121号':'深圳市罗湖区深南东路1234号',
                  state:'已驳回'
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      getRandomBirthdayBetween(){
        let date = new Date(),
        a = + new Date("January 01, 1980 00:00:00"),
        b = + new Date();
        date.setTime(Math.random()*(b - a) + a);
        return date;
      },
      //日期格式化
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatBirthday (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return '' + y + m  + d  ;
      },
      //生成手机号码
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      //返回选择数据
      PersonalTableData (data,index){
        this.Personal_info = data
        this.sendPersonalParams ()
        this.Personal_detail = true;
      },
      InstitutionalTableData (data,index){
        this.Institutional_info = data
        this.sendInstitutionalParams ()
        this.Institutional_detail = true;
      },
      //传递参数跳转
      sendPersonalParams () {
        this.table_list_show = false
      },
      sendInstitutionalParams () {
        this.table_list_show = false
      },
       //随机生成身份证号
       getId_no(){
         var coefficientArray = [ "7","9","10","5","8","4","2","1","6","3","7","9","10","5","8","4","2"];// 加权因子
         var lastNumberArray = [ "1","0","X","9","8","7","6","5","4","3","2"];// 校验码
         var AllCode = Area_code();
         let rom = Math.random()*(3465-1)+1;
         var address = AllCode[parseInt(rom)]; // 住址
         var birthday = this.formatBirthday (this.getRandomBirthdayBetween()); // 生日
         var s = Math.floor(Math.random()*10).toString() + Math.floor(Math.random()*10).toString() + Math.floor(Math.random()*10).toString();
         var array = (address + birthday + s).split("");
         var total = 0;
         for(let i in array){
              total = total + parseInt(array[i])*parseInt(coefficientArray[i]);
         }

         var lastNumber = lastNumberArray[parseInt(total%11)];
         var id_no_String = address + birthday + s + lastNumber;

          return id_no_String;

      }
    },
    created(){
         this.handleListPersonal();
         this.handleListInstitutional();
    }
}
</script>

<style lang="css">
.Reject-list {
  background-color: #fff;
  padding: 20px;
}
.Reject-list .search_criteria{
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Reject-list .search_criteria p{
  height: 30px;
}

.Reject-list .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  background-color: #fff;
  text-align: center;
}
.Reject-list .Button-tab{
  width: 600px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 16px;
  border: 1px solid rgba(215, 215, 215, 1);
  border-right: none;
  margin-bottom: 20px;
  background-color: #fff;
}
.Reject-list .Button-tab .left{
  width: 299px;
  height: 48px;
  border-right: 1px solid rgba(215, 215, 215, 1);
  cursor: pointer;
}
.Button-tab>.active{
  background-color:  #409EFF;
  color: #fff;
}
.Reject-list .personal-detail{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: left;
}
.Reject-list .personal-detail button{
  width: 80px;
  height: 35px;
}
.Reject-list .personal-name{
  height: 50px;
  line-height: 50px;
  text-align: left;
  border-bottom: 1px solid #D7D7D7;
}
.Reject-list .personal-certificates{

}
.Reject-list .personal-certificates h4{
  padding-left: 90px;
}
.Reject-list .personal-certificates button{
  margin-left: 90px;
}
.Reject-list .personal-certificates .upload{
  width: 238px;
  height: 130px;
  border: 1px solid #D7D7D7;
  line-height: 130px;
  margin-bottom: 20px;
  text-align: center;
}
.Reject-list .personal-certificates .upload img{
  width: 236px;
  height: 132px;
  vertical-align: middle;
}
.personal-certificates .font-16{
  line-height: 80px;
}
.Reject-list .Reject-enterprise{

}
.Reject-enterprise .upload{
  width: 238px;
  height: 130px;
  border: 1px solid #D7D7D7;
  line-height: 130px;
  margin-bottom: 20px;
  margin-top: 10px;
}
.Reject-enterprise .upload img{
  width: 238px;
  height: 130px;
  vertical-align: middle;
}
.Reject-enterprise .enterprise-title{
  text-align: left;
  line-height: 50px;
  padding-left: 18px;
}
.Reject-enterprise .basic-info{

}
.Reject-enterprise .basic-info tr{

}
.Reject-enterprise .basic-info th{
  width: 200px;
  height: 30px;
  text-align: right;
  padding-right: 20px;
}
.Reject-enterprise .basic-info td{
  width: 200px;
  text-align: left;
  padding-left: 20px;
}
.Reject-list .success{
  font-size: 16px;
  font-weight: bold;
  color: red;
  line-height: 50px;
}
.Reject-list .success i {
  line-height: 50px;
  margin-right: 10px;
}
.Reject-list .Reject-reason{
  text-align: left;
  padding:0 20px;
}
.Reject-list .Reject-reason p{
  margin-bottom: 15px;
}
.Reject-list .reason-box{
  width: 600px;
  height: 100px;
  border: 1px solid #D7D7D7;
  padding: 10px;
}
</style>



